<template>
  <div class="detail">

    <div class="box-top">
      <div class="box-pic">

        <div class="swiper-container gallery-top">
          <div class="swiper-wrapper">
            <!--<div class="swiper-slide">-->
              <!--<div class="detail-pic-large">1-->
                <!--<img src="../../common/images/detail/p-large.jpg"/>-->
              <!--</div>-->
            <!--</div>-->
            <div class="swiper-slide" v-for="item in picList">
              <div class="detail-pic-large">
                <img :src="URL_BASE + '/' +item"/>
              </div>
            </div>
          </div>
          <!-- Add Arrows -->
          <!--<div class="swiper-button-next "></div>-->
          <!--<div class="swiper-button-prev "></div>-->
        </div>
        <div style="width: 600px; position:relative">
        <div class="swiper-container gallery-thumbs"  style="width: 500px; margin: 0 auto">
          <div class="swiper-wrapper" >
            <!--<div class="swiper-slide" >-->
              <!--<div class="detail-pic-s">-->
                <!--<img src="../../common/images/detail/p-small.jpg"/>-->
              <!--</div>-->
            <!--</div>-->
            <div class="swiper-slide" v-for="item in picList">
              <div class="detail-pic-s">
                <img :src="URL_BASE +'/' +item"/>
              </div>
            </div>

          </div>

        </div>
        <div class="swiper-button-next"></div>
        <div class="swiper-button-prev "></div>
        </div>

      </div>

      <div class="box-desc">
        <h2>{{detail.product_name}}</h2>
        <h3>纯银纪念章</h3>
        <div class="descript">
          <span class="desc-title">
            产品描述：
          </span>
          <span class="desc-cont">
            {{detail.descrip}}
          </span>
        </div>
        <div class="pro-list">
          <ul>
            <li>材质：{{detail.material}}</li>
            <li>克重：{{detail.weight}}</li>
            <li>规格：{{detail.spec}}</li>

          </ul>
        </div>


        <div class="pro-use">用途：{{detail.purpose}}</div>

        <div class="pro-name"> 御泉黄金</div>

        <div class="pro-contact">
          需要协助 <span class="icon-tel"></span>400-880-5023/18321915773 或 <span class="icon-query"></span>线上查询
        </div>


      </div>


    </div>
    <div class="clearfix"></div>
    <div class="box-line">
      <img src="../../common/images/line-hr.gif"/>
    </div>

    <div class="detail-container" v-html="detail.detail">

    </div>

    <!--<div class="pro-pics">-->
      <!--<img src="../../common/images/detail/d1.jpg"/>-->
      <!--<img src="../../common/images/detail/d2.jpg"/>-->
      <!--<img src="../../common/images/detail/d3.jpg"/>-->
    <!--</div>-->

    <!--<div class="pro-look">-->
      <!--<h2>国家权威检测</h2>-->
      <!--<h3>国家金银制品质量监督检验中心出具检测报告，足金足银保证。</h3>-->

    <!--</div>-->
    <!--<div class="pro-pics" style="padding-top: 100px">-->
      <!--<img src="../../common/images/detail/d4.jpg"/>-->
    <!--</div>-->

    <div class="clearfix"></div>
    <Message></Message>
    <CaseMore></CaseMore>
  </div>
</template>
<script>
  import {PopAlert, ShowLoading, HideLoading} from 'common/js/mylayer'
  import {URL_BASE} from 'common/js/public'
  import {MyPost} from 'common/js/request'
  import Swiper from 'swiper';
  import 'swiper/dist/css/swiper.min.css';
  import Message from  '@/components/message/message'
  import CaseMore from  '@/components/case-more/case-more'
  export default {
    data(){
      return{
        id:1,
        detail:{},
        URL_BASE:URL_BASE,
        picList:[]
      }
    },
    created(){
      if(this.$route.query.id){
        this.id= this.$route.query.id
      }

      this.getDetail()

    },
    methods:{
      getDetail() { // 初始请求
        MyPost(URL_BASE+'/index.php/api/product/getDetail',
          {
            id:this.id,
            device:'pc'
          }).then(res => {
          console.log(res)
          if (res.return_code === 0) {
            this.detail = res.data;
            this.picList = res.data.marque;
          }else{
            PopAlert(res.return_message)
          }
        });

      }
    },
    updated(){
      var galleryTop = new Swiper('.gallery-top', {
        spaceBetween: 10,
        loop:true,
        loopedSlides: 4, //looped slides should be the same
//        navigation: {
//          nextEl: '.swiper-button-next',
//          prevEl: '.swiper-button-prev',
//        },
      });
      var galleryThumbs = new Swiper('.gallery-thumbs', {
        spaceBetween: 10,
        slidesPerView: 4,
        touchRatio: 0.2,
        loop: true,
        loopedSlides: 4, //looped slides should be the same
        slideToClickedSlide: true,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
      });
      galleryTop.controller.control = galleryThumbs;
      galleryThumbs.controller.control = galleryTop;
    },
    mounted(){
      document.documentElement.scrollTop = document.body.scrollTop =0;
    },
    components:{
      Message,
      CaseMore
    }

  }



</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
.detail
  .swiper-button-prev
    background-image:url("../../common/images/jt-l.gif");
  .swiper-button-next
    background-image:url("../../common/images/jt-r.gif");
  .box-top
    width 1200px
    margin 0 auto
    margin-top 70px
    border-top 1px solid #ebe9ea
    padding-top 40px
    .box-pic
      float left
      width 600px
      height 630px
      text-align center
      .detail-pic-s
        margin 0 10px
        img
          border 1px solid #e8e8e8
          width 106px
          height 106px
      .detail-pic-large
        img
          width 463px
          height: 445px
          border 1px solid #e8e8e8
    .box-desc
      float right
      width 490px
      h2
        font-size 36px
        line-height 50px
      h3
        font-size 24px
        line-height 40px
      .descript
        margin-top 55px
        margin-bottom 12px
        padding 10px
        background-color #f5f5f5
        font-size 18px
        line-height 24px
        cursor #111111
      .pro-list
        background-color #f5f5f5
        padding 5px
        ul
          background-color #fff
          padding 10px 50px 10px 10px
          overflow hidden
          li
            line-height 40px
            border-bottom 1px solid #ebe9ea
            text-align left
            display block
            color #976e01
            font-size 18px
            &:last-child
              border-bottom none

      .pro-use
        line-height 20px
        padding 10px
        border 1px solid #ebe9ea
        font-size 18px
        margin-top 12px
        margin-bottom 50px
      .pro-name
        font-size 18px
        color #ffffff
        background-color #000
        line-height 52px
        text-align center
      .pro-contact
        padding-top 24px
        text-align center
        color #747474

  .box-line
    text-align center
    padding-top 108px
    padding-bottom 50px
  .detail-container
    width 1200px
    margin 0 auto
    padding-bottom 20px
    p
      margin-bottom 20px
    img
      display block
      width 100%
      margin-bottom 10px

  .pro-pics
    width 1200px
    margin 0 auto
    padding-bottom 20px
    img
      display block
      width 100%
      margin-bottom 45px

  .pro-look
    background-color #e4e4e4
    height 254px
    text-align center
    h2
      font-size 60px
      padding-top 70px
      padding-bottom 20px
    h3
      font-size 40px
</style>





